.additional-page{
    margin-top: 41px !important;
    padding-bottom: 100px;
    &__title{
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        color: #000000;
        margin-bottom: 10px;
    }

    &__subtitle{
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        color: #000000;
        margin-bottom: 42px;
    }

    .card-wrapper-block{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 21px;

        .single-card{
            position: relative;
            z-index: 55;
            cursor: pointer;
            &::before{
                content: '';
                position: absolute;
                top: -10px;
                right: 25px;
                z-index: 49;
                width: 40px;
                height: 40px;
                border-radius: 50%;
                background-repeat: no-repeat;
                background-image: url(../../assets/images/true.svg);
                background-size: 16px 12px;
                background-position: center;
                background-color: #fff;
                border: 1px solid #41D481;
                box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.0860224);
            }
        }
        .opacity-card{
            opacity: 0.5;
            display: flex;
        }
    }

    .edit-block{
        display: flex;
        align-items: center;
        width: 100%;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        padding: 13px 24px;
        box-sizing: border-box;
        border-radius: 5px;
        box-shadow: 12px 4px 24px rgb(0 0 0 / 6%);
        position: relative;

        &::before{
            content: "";
            position: absolute;
            top: -11px;
            left: 16%;
            margin: auto;
            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-bottom: 10px solid rgba(0, 0, 0, 0.209296);
        }

        &__title{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            margin-right: 15px;
        }

        &__address{
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 19px;
            color: #19D476;
            margin-right: 13px;
        }

        &__btn{
            background: #FFFFFF;
            border: 1px solid #41D481;
            box-sizing: border-box;
            border-radius: 100px 5px 5px 100px;
            outline: none;
            padding: 8px 19px 10px 47px;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            position: relative;
            cursor: pointer;

            &::before{
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                margin: auto;
                width: 37px;
                height: 37px;
                border-radius: 50%;
                background-repeat: no-repeat;
                background-image: url(../../assets/images/pen.svg);
                background-size: 17px 17px;
                background-position: center;
                background-color: #41D481;
            }
        }
    }

    .additional-form{
        margin-top: 58px;
        outline: none;
        &-top{
            display: flex;
            align-items: flex-end;
            margin-bottom: 48px; 

            .label-input{
                display: flex;
                flex-direction: column;
            }
        }

        .additional-label{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 17px;
            color: #000000;
            margin-bottom: 13px;
        }

        .additional-input{
            border: none;
            border-bottom: 1px solid #979797;
            padding-bottom: 7px;
            margin-right: 43px;

            &:focus-visible{
                border-color: transparent !important;
            }

            &::placeholder{
                font-style: normal;
                font-weight: 500;
                font-size: 19px;
                line-height: 23px;
                color: #000000;
            }
        }

        .additional-select{
            border: none;
            border-bottom: 1px solid #979797;
            padding-bottom: 6px;
            font-style: normal;
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
            color: #000000;
            margin-right: 26px;
            &:focus-visible{
                border-color: transparent !important;
            }
        }

        .input-check{
            width: 24px !important;
            height: 24px !important;
            margin-right: 11px;
        }

        .check-label{
            font-style: normal;
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
            color: #000000;
        }

        .additional-title{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 17px;
            color: #000000;
            margin-bottom: 21px;
        }

        .check-block{
            margin-bottom: 48px;
            // display: flex;
            // justify-content: space-between;
            // flex-wrap: wrap;

            input,label{
                display: block;
            }

            .check-div{
                display: flex;
                width: 33%;
                margin-bottom: 33px;
            }

            .check-bottom{
                display: flex;
            }

            .checblock-top{
                display: flex;
            }

        }
    }

    .abt-home{
        display: flex;
        margin-bottom: 48px;
        div{
            display: flex;
            flex-direction: column;
        }
    }

    .possible{
        padding-bottom: 133px;

    }

    .form-bottom{
        padding-top: 10px;
        border-top: 1px solid #979797;

        .additional-title{
            margin-bottom: 14px;
        }

        .img-loader-block{
            &-top{
                display: flex;
            justify-content: space-between;
            }
            .img-loader{
                margin-bottom: 23px;
                input{
                    display: none;
                }
                label{
                    background: rgba(180, 226, 225, 0.350232);
                    border: 1px solid #50E3C2;
                    box-sizing: border-box;
                    border-radius: 9px;
                    width: 107px;
                    height: 107px;
                    position: relative;
                    display: block;
                    &::before{
                        content: '';
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        right: 0;
                        left: 0;
                        margin: auto;
                        width: 42px;
                        height: 36px;
                        background-repeat: no-repeat;
                        background-image: url(../../assets/images/camera.svg);

                    }
                }
            }
        }
    }

    .additional-footer{
        margin-top: 20px;
        &__title{
            font-style: normal;
            margin-bottom: 15px;
            font-weight: 500;
            font-size: 24px;
            line-height: 29px;
            color: #000000;
        }

        .iframe-block{
            margin-bottom: 22px;
        }

        .label-input{
            display: flex;
            flex-direction: column;
            margin-bottom: 31px;

            input{
                width: 300px;
            }
        }

        .flex-blockk{
            display: flex;
            align-items: center;

            .add-new{
                background: #19D476;
                border-radius: 13px;
                padding: 21px 46px;
                margin-right: 67px;
                font-style: normal;
                font-weight: 500;
                font-size: 19px;
                line-height: 23px;
                color: #000000;
                border: none;
                outline: none;
                cursor: pointer;
            }

            .add-new-view{
                font-style: normal;
                font-weight: 500;
                font-size: 19px;
                line-height: 23px;
                text-decoration-line: underline;
                color: #4E72CA;
            }
        }
    }
}