.login{
    &-inner{
        height: 812px !important;
        display: flex;
        justify-content: space-between;

        &-left{
            margin-top: 121px;
            &__title{
                font-style: normal;
                font-weight: bold;
                font-size: 24px;
                line-height: 30px;
                text-align: center;
                text-transform: uppercase;
                color: #000000;
                margin-bottom: 60px;
            }

            .search-acc{
                width: 329px;
                font-style: normal;
                font-weight: bold;
                font-size: 16px;
                line-height: 20px;
                text-align: center;
                color: #4E72CA;
                display: block;
            }

            .or{
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                text-align: center;
                color: #787993;
                position: relative;
                width: 329px;
                margin-bottom: 26px;

                &::before{
                    content: '';
                    width: 140px;
                    height: 2px;
                    background-color: #979797;
                    left: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    position: absolute;
                }

                &::after{
                    content: '';
                    width: 140px;
                    height: 2px;
                    background-color: #979797;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    position: absolute;
                }
            }
        }

        &-right{
            height: 100%;
            width: 50%;
            background-repeat: no-repeat;
            background-image: url(../../assets/images/bgi-rec.png);
            background-size: cover;
            position: relative;

            .open-place{
                padding: 79px 40px 40px 41px;
                box-sizing: border-box;
                background: #FFFFFF;
                border-radius: 220px 220px 220px 25px;
                width: 285px;
                position: absolute;
                left: 61px;
                bottom: 165px;
                &__img{
                    margin-bottom: 8px;
                    width: 50px;
                    height: 26px;
                }

                &__text{
                    font-style: normal;
                    font-weight: bold;
                    font-size: 19px;
                    line-height: 21px;
                    color: #787993;
                    width: 213px;
                    margin-bottom: 19px;
                }

                &__btn{
                    border: 2px solid #41D481;
                    box-sizing: border-box;
                    border-radius: 5px;
                    background-color: #fff;
                    outline: none;
                    padding: 9px 18px 11px 19px;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 16px;
                    line-height: 20px;
                    color: #41D481;
                    cursor: pointer;
                }
            }
        }

        &-form{
            display: flex;
            flex-direction: column;
            &__label{
                font-style: normal;
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                color: #787993;
                margin-bottom: 7px;
            }

            &__input{
                background: #FFFFFF;
                border: 1px solid rgba(151, 151, 151, 0.482592);
                border-radius: 5px;
                width: 329px;
                padding: 10px 27px;
                box-sizing: border-box;
                margin-bottom: 28px;

                &::placeholder{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 23px;
                    color: #C7C7C7;
                }
            }

            &__btn{
                width: 329px;
                padding: 21px 0;
                background: #41D481;
                border-radius: 5px;
                font-style: normal;
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                text-align: center;
                color: #FFFFFF;
                border: none;
                outline: none;
                cursor: pointer;
                margin-bottom: 49px;
            }
        }
    }
}