.single-page{
    &-top{
        display: flex;
        padding: 24px 0;

        span{
            margin-right: 20px;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            color: #787993;
        }

        &__bread-crumb{
            margin-right: 20px;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            color: #787993;
            cursor: pointer;
        }
    }

    .slick-list{
        width: 1140px;
    }

    &-bottom{
        .title-wrapper{
            display: flex;
            margin-bottom: 20px;
        }

        .single-card-block{
            display: flex;
            justify-content: space-between;
            padding-bottom: 55px !important;
                margin-bottom: 31px;
                .slick-arrow.slick-prev{
                    position: absolute !important; 
                    z-index: 6;
                    left: 0 !important;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    cursor: grab !important;
                    background-color: #fff !important;
                    border-radius: 50%;
                    width: 34px;
                    height: 34px;
                    box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
        
                    &::before{
                        content: '';
                        width: 25px;
                        height: 25.26px;
                        background-image: url(../../assets/images/carousel-arrow.svg);
                        position: absolute;
                        z-index: 99;
                        background-repeat: no-repeat;
                        top: 14px;
                        bottom: 0;
                        margin: auto;
                        right: 0;
                    }
                    
                    &:active{
                        cursor: grabbing !important;
                    }
                }
            
                .slick-arrow.slick-next{
                    position: absolute !important;
                    z-index: 6;
                    right: 20px !important;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    cursor: grab !important;
                    background-color: #fff !important;
                    border-radius: 50%;
                    width: 34px;
                    height: 34px;
                    box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
        
                    &::before{
                        content: '';
                        width: 25px;
                        height: 25.26px;
                        background-image: url(../../assets/images/carousel-arrow.svg);
                        position: absolute;
                        z-index: 99;
                        background-repeat: no-repeat;
                        top: 0px;
                        bottom: 7px;
                        margin: auto;
                        left: 0;
                        transform: rotate(180deg);
                    }
                    cursor: grab !important;
                    &:active{
                        cursor: grabbing !important;
                    }
                }
        }
    }

    &-center{
        &__title{
            font-style: normal;
            font-weight: 500;
            font-size: 34px;
            line-height: 41px;
            color: #414D6A;
            margin-bottom: 14px;
        }

        .product-block{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            img{
                display: block;
            }

            &-right{
                width: 45%;
                &-top{
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 17px;

                    &__item{
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 17px;
                        letter-spacing: -5.89155e-05px;
                        color: #414D6A;
                    }
                }

                &__price{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 40px;
                    line-height: 49px;
                    letter-spacing: -0.00016833px;
                    color: #444444;
                    margin-bottom: 8px;
                }

                &__place{
                    font-size: 14px;
                    line-height: 16px;
                    letter-spacing: -5.89154e-05px;
                    text-decoration-line: underline;
                    color: #4E72CA;
                    margin-bottom: 14px;
                    display: block;
                }

                .contact{
                    margin-bottom: 25px;
                    &__btn{
                        background: #304FFF;
                        border-radius: 23.5px;
                        padding: 15px 28px;
                        border: none;
                        outline: none;
                        margin-right: 11px;
                        font-size: 14px;
                        line-height: 16px;
                        letter-spacing: -5.89155e-05px;
                        color: #FFFFFF;
                        cursor: pointer;
                    }

                    &__btnn{
                        background: #4E72CA;
                        border-radius: 23.5px;
                        padding: 15px 32px;
                        border: none;
                        outline: none;
                        font-size: 14px;
                        line-height: 16px;
                        letter-spacing: -5.89155e-05px;
                        color: #FFFFFF;
                        cursor: pointer;
                    }
                }

                .table-bottom{
                    margin-bottom: 10px;
                    border-left: 1px solid rgba(151, 151, 151, 0.191604);
                    .table-item__text{
                        width: 500px !important;
                    }
                }

                .table{
                    display: flex;
                    &-left{
                        border-left: 1px solid rgba(151, 151, 151, 0.191604);
                        .table-item{
                            border-right: 1px solid rgba(151, 151, 151, 0.191604);
                            border-bottom: 1px solid rgba(151, 151, 151, 0.191604);
                            padding-right: 0px;
                        }
                    }
                    &-center{
                        .table-item{
                            padding-right: 30px;
                            border-right: 1px solid rgba(151, 151, 151, 0.191604);
                            border-bottom: 1px solid rgba(151, 151, 151, 0.191604);
                        }
                    }
                    &-right{
                        .table-item{
                            padding-right: 0px;
                            border-bottom: 1px solid rgba(151, 151, 151, 0.191604);

                            &__title{
                                width: 115px;
                            }

                            &__text{
                                width: 140px;
                                font-size: 16px;
                            }
                        }
                    }
                    &-item{
                        &.small{
                            padding-bottom: 43px;
                        }
                        padding: 19px 32px 21px 15px;
                        box-sizing: border-box;
                        &__title{
                            font-size: 14px;
                            line-height: 16px;
                            letter-spacing: -0.00016833px;
                            color: #444444;
                            margin-bottom: 5px;
                            width: 140px;
                        }

                        &__text{
                            font-style: normal;
                            font-weight: 500;
                            font-size: 18px;
                            line-height: 22px;
                            letter-spacing: -0.00016833px;
                            color: #444444;
                            &.special{
                                font-style: normal;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 22px;
                                letter-spacing: -0.00016833px;
                                text-decoration-line: underline;
                                color: #4E72CA;
                                width: 125px;
                                cursor: pointer;
                            }
                        }
                    }
                }

                .author{
                    display: flex;
                    align-items: center;
                    margin-bottom: 24px;

                    &__title{
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 17px;
                        letter-spacing: -3.17237e-05px;
                        color: #000000;
                        margin-right: 7px;
                    }

                    &__name{
                        font-style: normal;
                        font-weight: bold;
                        font-size: 14px;
                        line-height: 17px;
                        letter-spacing: -4.13787e-05px;
                        text-decoration-line: underline;
                        color: #2AAE63;
                    }
                }

                .customer-phone{
                    margin-bottom: 7px;
                    display: flex;
                    background: #65E99F;
                    align-items: center;
                    border-radius: 18.5px;
                    padding: 0 0 0 23px;
                    display: inline-flex;

                    &.mb{
                        margin-bottom: 17px;
                    }

                    &__number{
                        font-style: normal;
                        font-weight: bold;
                        font-size: 16px;
                        line-height: 20px;
                        letter-spacing: -6.73319e-05px;
                        color: #000000;
                        margin: 12px;
                    }

                    &__btn{
                        background: #41D481;
                        border-radius: 18.5px;
                        padding: 9px 26px 10px 19px;
                        border: none;
                        cursor: pointer;
                    }
                }
            }

            &-left{
                display: flex;
                align-items: center;
                .product-img-block{
                    display: flex;
                    flex-direction: column;
                    margin-right: 20px;
                    overflow: hidden;
                    width: 85px;
                }
                
            }

            .customer-about{
                &__text{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 17px;
                    line-height: 25px;
                    letter-spacing: -3.85217e-05px;
                    color: #414D6A;
                    margin-bottom: 18px;
                }
            }

            .add-info{
                margin-bottom: 97px;
                display: flex;
                justify-content: space-between;

                &__left{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 17px;
                    letter-spacing: -5.89155e-05px;
                    color: #000000;
                }

                &__right{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 17px;
                    letter-spacing: -5.89155e-05px;
                    color: red;
                }
            }
        }
    }
}