.choose {
    &-top {
        padding-top: 32px;
        border-bottom:  1px solid rgba(151, 151, 151, 0.393739);

        &-inner {
            &-header {
                width: 100%;
                border: 1px solid rgba(151, 151, 151, 0.496575);
                padding: 16px 16px 16px 58px;
                box-sizing: border-box;
                background: #FFFFFF;
                border-radius: 6px;
                position: relative;
                margin-bottom: 12px;

                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 17px;
                    margin: auto;
                    width: 23.8px;
                    height: 24.72px;
                    background-repeat: no-repeat;
                    background-image: url(../../assets/images/lupa.svg);
                }

                &__input {
                    width: 100%;
                    border: none;
                    outline: none;
                }
            }

            &-location {
                display: flex;
                align-items: center;
                margin-bottom: 25px;

                &__btn {
                    font-style: normal;
                    font-weight: bold;
                    font-size: 13px;
                    line-height: 16px;
                    color: #704FFF;
                    background: #E5E3FF;
                    border-radius: 3px;
                    padding: 3px 11px;
                    margin-right: 13px;
                    border: none;
                    cursor: pointer;

                    &:last-child {
                        margin-right: 0;
                    }

                    &.top {
                        color: #57B4E2;
                        background: #E4F6FF;
                    }

                    &.perfect {
                        color: #47B95E;
                        background: #D2FFDB;
                    }

                    &.g-price {
                        color: #874C1A;
                        background: #F6DAC2;
                    }

                    &.place-town {
                        color: #787993;
                        background: #D8D8D8;
                        padding-right: 31px;
                        position: relative;

                        &::after {
                            content: '';
                            position: absolute;
                            right: 10px;
                            width: 7px;
                            height: 10px;
                            top: 0;
                            bottom: 0;
                            margin: auto;
                            background-repeat: no-repeat;
                            background-image: url(../../assets/images/pin.svg);
                        }
                    }
                }
            }

            &-rubric {
                display: flex;
                align-items: center;
                margin-bottom: 23px;

                &-left {
                    margin-right: 54px;

                    &__title {
                        font-size: 16px;
                        line-height: 19px;
                        letter-spacing: -6.7332e-05px;
                        color: #787993;
                    }
                }

                &-right {
                    display: flex;
                    align-items: center;
                    background: #FFFFFF;
                    border: 1px solid rgba(151, 151, 151, 0.496575);
                    box-sizing: border-box;
                    border-radius: 27px;
                    padding: 8px;
                    box-sizing: border-box;
                    width: 472px;
                    position: relative;

                    &::after {
                        content: '';
                        position: absolute;
                        right: 24px;
                        width: 18px;
                        height: 11px;
                        top: 0;
                        bottom: 0;
                        margin: auto;
                        background-repeat: no-repeat;
                        background-image: url(../../assets/images/arrow-b.svg);
                    }

                    .electronic {
                        background: #EBF5FF;
                        border: 1px solid #B3D9FF;
                        box-sizing: border-box;
                        border-radius: 20.5px;
                        overflow: hidden;
                        padding: 11px 47px 12px 20px;
                        margin-right: 7px;
                        cursor: pointer;
                        position: relative;

                        &::after {
                            content: '';
                            position: absolute;
                            right: -2px;
                            top: 0;
                            bottom: 0;
                            margin: auto;
                            width: 42px;
                            height: 42px;
                            border-radius: 50%;
                            background: #EBF5FF;
                            border: 1px solid #B3D9FF;
                            box-sizing: border-box;
                            background-repeat: no-repeat;
                            background-image: url(../../assets/images/x.svg);
                            background-position: center;
                        }

                        &__title {
                            font-style: normal;
                            font-weight: 500;
                            font-size: 15px;
                            line-height: 18px;
                            color: #2979FF;
                        }
                    }
                }
            }

            &-price{
                display: flex;
                margin-bottom: 30px;
                &-left{
                    margin-right: 41px;

                    &__title{
                        font-size: 16px;
                        width: 250px;
                        line-height: 19px;
                        letter-spacing: -6.7332e-05px;
                        color: #787993;
                    }
                }

            }

            &-bottom{
                display: flex;
                justify-content: space-between;
                padding-bottom: 30px;

                &-left{
                    display: flex;

                    select{
                        border: none !important;
                        margin-right: 70px;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 14px;
                        line-height: 17px;
                        outline: none;
                        color: #414D6A;
                        &:last-child{
                            margin-right: 0;
                        }
                    }
                }

                &-right{
                    .apply{
                        font-style: normal;
                        font-weight: bold;
                        font-size: 15px;
                        line-height: 19px;
                        text-align: center;
                        color: #FFFFFF;
                        padding: 12px 18px;
                        background: #304FFF;
                        box-shadow: 3px 6px 14px #9CC1FF;
                        border-radius: 22px;
                        border: none;
                        cursor: pointer;
                    }
                }
            }
        }
    }
    &-center{
        padding-top: 23px;
        &-inner{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            padding-bottom: 54px;
            border-bottom: 1px solid rgba(182, 202, 251, 0.606827);

            .sale-card{
                width: 262px !important;
            }
        }
        .view-more{
            padding-top: 18px;
            margin-bottom: 32px;
            text-align: center;

            &__title{
                font-size: 15px;
                line-height: 18px;
                color: #4E72CA;
                display: inline-block;
                position: relative;

                &::before{
                    content: '';
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    right: -20px;
                    background-repeat: no-repeat;
                    background-image: url(../../assets/images/bottom-arrow.svg);
                    width: 12px;
                    height: 8px;
                }
            }
        }

        .now-wanted{
            padding-bottom: 40px;

            &__title{
                font-style: normal;
                font-weight: bold;
                font-size: 20px;
                line-height: 25px;
                color: #414D6A;
                position: relative;
                padding-left: 25px;
                margin-bottom: 29px;

                &.g--title{
                    padding-left: 0;
                    margin-bottom: 18px;
                    color: #41D481;
                    &::before{
                        display: none;
                    }
                }

                &::before{
                    content: '';
                    top: 0;
                    position: absolute;
                    bottom: 0;
                    margin: auto;
                    left: 0;
                    background-repeat: no-repeat;
                    width: 20px;
                    height: 20px;
                    background-size: contain;
                    background-image: url(../../assets/images/lupa.svg);
                }
            }

            &-slider{
                padding: 21px 38px;
                box-sizing: border-box;
                border-left: 1px solid rgba(120, 121, 147, 0.243461);
                border-right: 1px solid rgba(120, 121, 147, 0.243461);
                margin-bottom: 38px;
            }


        }
    }

    .now-wanted-slider-last{
        .slick-arrow.slick-next {
            position: absolute !important;
            z-index: 6;
            right: -1% !important;
            top: 0;
            bottom: 0;
            overflow: hidden;
            margin: auto;
            cursor: grab !important;
            background: #2979FF !important;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331) !important;
            border-radius: 50%;
            width: 34px;
            height: 34px;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);

            &::before {
                content: '';
                width: 45px;
                height: 25.26px;
                background-image: url(../../assets/images/right-arrow.svg);
                position: absolute;
                z-index: 99;
                background-repeat: no-repeat;
                top: 3px;
                bottom: 0;
                margin: auto;
                left: -10px;
            }

            cursor: grab !important;

            &:active {
                cursor: grabbing !important;
            }
        }

        .slick-arrow.slick-prev{
            display: none !important;
        }
    }
}