.container{
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
}

.header{
    background: #FFFFFF;
    box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
    padding: 17px 0;
    &-inner{
        .auth{
            display: flex;
            align-items: center;
            &__name{
                font-style: normal;
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                text-align: center;
                color: #787993;
                margin-right: 15px;
            }
            &__img{
                width: 51px;
                height: 51px;
                border-radius: 50%;
                object-fit: cover;
            }
        }
        &-top{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 16px;

            &-left{
                .logo-wrapper{
                    &__img{
                        width: 67px;
                        height: 67px;
                    }
                }
            }

            &-center{
                display: flex;
                align-items: center;

                &__link{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #000000;
                    display: block;
                    margin-right: 15px;
                    &:last-child{
                        margin-right: 0;
                    }
                }
            }

            &-right{
                display: flex;
                align-items: center;

                &-language{
                    margin-right: 66px;
                    &__item{
                        background-color: transparent;
                        border: none;
                        outline: none;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 17px;
                        color: #4A4A4A;
                        font-family: 'Mulish' !important;
                        cursor: pointer;

                        &:first-child{
                            border-right: 1px solid #4A4A4A;
                        }
                    }
                }

                &-login{
                    display: flex;
                    &-sign{
                        border: none;
                        outline: none;
                        background-color: transparent;
                        cursor: pointer;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 16px;
                        line-height: 21px;
                        color: #4E72CA;
                        display: block;
                        font-family: 'Mulish' !important;

                        &:first-child{
                            border-right: 1px solid #4E72CA;
                            padding-right: 10px;
                            margin-right: 10px;
                        }
                    }
                }
            }
        }

        &-bottom{
            display: flex;
            justify-content: space-between;
            align-items: center;

            &.delete{
                display: none;
            }

            &-right{
                &__btn{
                    background: #19D476;
                    border-radius: 22px;
                    padding: 12px 30px 13px 56px;
                    border: none;
                    outline: none;
                    position: relative;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 15px;
                    line-height: 19px;
                    cursor: pointer;
                    color: #FFFFFF;

                    &:hover{
                        color: #fff;
                    }

                    &::before{
                        content: '';
                        position: absolute;
                        left: 0;
                        top: 0;
                        margin: auto;
                        bottom: 0;
                        background: #11B463;
                        width: 44px;
                        height: 44px;
                        border-radius: 50%;
                        background-repeat: no-repeat;
                        background-image: url(../../assets/images/+.svg);
                        background-position: center;
                    }
                }
            }

            &-left{
                background: #FFFFFF;
                border: 1px solid rgba(151, 151, 151, 0.496575);
                box-sizing: border-box;
                border-radius: 22.5px;
                overflow: hidden;

                &-select{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #000000;
                    padding: 13px 44px 14px 30px;
                    border: none;
                    outline: none;
                    border-right: 1px solid rgba(151, 151, 151, 0.496575);
                    font-family: 'Mulish', sans-serif !important;

                    &.last{
                        border-left: 1px solid rgba(151, 151, 151, 0.496575);
                        border-right: none;
                    }

                    &__item{
                        background-color: #000;
                        color: #fff;
                    }
                }

                &__input{
                    border: none;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #000000 !important;
                    outline: none;
                    padding-left: 25px;
                    &::placeholder{
                    color: #000000 !important;
                    font-family: 'Mulish', sans-serif !important;
                    }
                }

                &__btn{
                    border: 1px solid transparent;
                    outline: none;
                    background: #19D476;
                    border-radius: 22px;
                    cursor: pointer;
                    padding: 14px 19px;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 17px;
                    color: #FFFFFF;
                    font-family: 'Mulish', sans-serif !important;

                    &:hover{
                        color: #fff;
                    }
                }
            }
        }
    }
}