.footer{
    background: #33383C;

    &-more{
        text-align: center;
        padding-bottom: 34px !important;
        border-bottom: 1px solid #4C565E;

        &__btn{
            font-size: 15px;
            line-height: 18px;
            color: #66717A;
            border: none;
            outline: none;
            background-color: transparent;
            cursor: pointer;
            position: relative;

            &::after{
                content: '';
                position: absolute;
                right: -20px;
                top: 2px;
                bottom: 0;
                margin: auto;
                background-repeat: no-repeat;
                width: 16px;
                height: 10px;
                background-image: url(../../assets/images/bottom-arrow.svg);
            }
        }
    }

    &-bottom{
        padding: 52px 0 34px 0 !important;
        background: #33383C;
        display: flex;
        justify-content: space-between;

        .link-wrapper{
            margin-bottom: 25px;
        }

        &-4{
            .link-wrapper:first-child{
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/orange-arrow.svg);
                }
            }
            .link-wrapper:last-child{
                margin-bottom: 0;
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/bluee-arrow.svg);
                }
            }
        }

        &-2{
            .link-wrapper:first-child{
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/yellow-arrow.svg);
                }
            } 
            .link-wrapper:last-child{
                margin-bottom: 0;
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/purple-arrow.svg);
                }
            }
        }

        &-3{
            .link-wrapper:first-child{
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/pink-arrow.svg);
                }
            } 

            .link-wrapper:last-child{
                margin-bottom: 0;
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/green-arrow.svg);
                }
            }
        }

        &-1{
            .link-wrapper:first-child{
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/pink-arrow.svg);
                }
            }

            .link-wrapper:last-child{
                margin-bottom: 0;
                .footer-bottom__title::before{
                    background-image: url(../../assets/images/green-arrow.svg);
                }
            }
        }

        &__title{
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            margin-bottom: 12px;
            color: #FFFFFF;
            position: relative;

            &::before{
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                margin: auto;
                left: -20px;
                width: 5.92px;
                height: 9.6px;
                background-repeat: no-repeat;
            }
        }

        &__link{
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 29px;
            display: block;
            color: #878F99;

            &:hover{
                color: #fff;
            }
        }
    }

    &-top{
        background: #282B30;
        padding: 23px 0;

        &-inner{
            display: flex;
            justify-content: space-between;
            align-items: center;

            &-left{
                display: flex;
                align-items: center;

                a{
                    display: block;
                    margin-right: 14px;
                }

                &__img{
                    width: 67px;
                    height: 67px;
                    opacity: 0.9;
                }

                &__text{
                    font-style: normal;
                    font-weight: bold;
                    width: 159px;
                    font-size: 14px;
                    line-height: 17px;
                    color: #FFFFFF;
                    mix-blend-mode: normal;
                    opacity: 0.1;
                }
            }

            &-center{
                &__title{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    width: 307px;
                    color: #98A4B7;
                }
            }

            &-right{
                display: flex;
                &-item{
                    &:first-child{
                        margin-right: 5px;
                    }
                }
            }
        }
    }

    &-nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 27px 0 !important;

        &__link{
            font-size: 15px;
            line-height: 18px;
            color: #878F99;
            display: block;

            &:hover{
                color: #05F3B3;
            }
        }
    }
}