.wanted-card{
    &__title{
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        color: #4E72CA;
    }

    &__publish{
        color: #4A4A4A;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        padding-left: 20px;
        line-height: 24px;
        position: relative;

        &::before{
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            width: 13px;
            height: 13px;
            background-repeat: no-repeat;
            background-image: url(../../assets/images/lupa.svg);
            background-size: contain;
        }
    }
}