.chat{
    display: flex;
    justify-content: space-between;

    &-left{
        width: 40%;
        background-color: rgba(182, 202, 251, 0.125113);


        &-chats{
            display: block;

            &.selected{
                background-color: #fff;
            }
        }

    }

    &-right{
        background-color: #fff;
        width: 60%;
    }
}