.chat-bar{
    display: flex;
    padding: 24px 24px 26px 14px;
    justify-content: space-between;
    background: rgba(182, 202, 251, 0.125113);
    align-items: center;
    border-right: 1px solid rgba(0, 0, 0, 0.0470165);

    &-left{
        display: flex;
        &__img{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            margin-right: 16px;
        }

        &-inner{
            &__name{
                font-style: normal;
                font-weight: 500;
                font-size: 12px;
                line-height: 15px;
                letter-spacing: 0.2px;
                color: #25252B;
                margin-bottom: 5px;
            }

            &__text{
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #25252B;
            }
        }
    }

    &-right{
        &__date{
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 15px;
            text-align: right;
            letter-spacing: 0.2px;
            color: #686871;
        }

        &__img{
            display: block;
            text-align: right;
            margin-left: auto;
        }
    }
}