.sale-card{
    margin-bottom: 36px;
    display: block;

    &-top{
        display: none;
    }

    &.top{
        position: relative;
        overflow: hidden;
        .sale-card__img{
            margin-bottom: 0;
        }
        .sale-card-bottom{
            background: rgba(65, 212, 129, 0.225289);
            border-radius: 6px;
            padding: 14px;
            box-sizing: border-box;
        }

        .sale-card-top{
            display: block;
            position: absolute;
            background: #41D481;
            transform: rotate(-40deg);
            padding: 5px 30px;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            color: #000000;
            transform: rotate(-41deg);
            top: 5px;
            left: -20px;
        }
    }


    &__img{
        width: 262px;
        height: 190px;
        object-fit: cover;
        margin-bottom: 11px;
    }

    &__title{
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        margin-bottom: 8px;
        width: 254px;
    }

    &__publish{
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        color: #4A4A4A;
        margin-bottom: 6px;
    }

    &__price{
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
    }

}