.profile{
    padding-top: 39px !important;
    background-color: #fff;
    padding-bottom: 16px;
    border-bottom: 1px solid #979797;
    &-inner{
        &-header{
            &-top{
                display: flex;
                justify-content: space-between;
                align-items: center;
                
                &-left{
                    &__title{
                        font-style: normal;
                        font-weight: bold;
                        font-size: 17px;
                        line-height: 21px;
                        text-align: center;
                        color: #414D6A;
                    }
                }
    
                &-right{
                    display: flex;
                    align-items: center;
    
                    &__title{
                        font-style: normal;
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        text-align: center;
                        color: #304FFF;
                        margin-right: 26px;
                    }

                    &__btn{
                        background: #FFFFFF;
                        border: 1px solid #41D481;
                        box-sizing: border-box;
                        border-radius: 100px 5px 5px 100px;
                        padding: 8px 22px 10px 47px;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 19px;
                        color: #000000;
                        position: relative;
                        cursor: pointer;

                        &::before{
                            content: '';
                            position: absolute;
                            left: 0;
                            top: 0;
                            bottom: 0;
                            margin: auto;
                            width: 36px;
                            height: 36px;
                            border-radius: 50%;
                            background: #41D481;
                            background-repeat: no-repeat;
                            background-size: 15px 15px;
                            background-position: center;
                            background-image: url(../../assets/images/plus.svg);
                        }
                    }
                }
            }
        }
    }
}

.profile-link{
    padding-top: 16px;
    border-bottom: 1px solid rgba(151, 151, 151, 0.600459);
    display: flex;
    margin-bottom: 5px;

    &__link{
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #000;
        margin-right: 50px;
        display: block;
        padding-bottom: 14px;

        &:last-child{
            margin-right: 0;
        }
        &.selected{
            color: #304FFF;
            border-bottom: 1px solid #304FFF;

        }
    }
}

.profile-main{
    background: rgba(0, 0, 0, 0.0470165);
    &-inner{
        padding-top: 15px;
        .footer{
            width: 100%;
            margin-top: 76px;
        }
        // padding-bottom: 76px;
        .menu-section{
            display: flex;
            margin-bottom: 11px;

            &__title{
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                color: #000000;

                &:first-child{
                    margin-right: 70px;
                }

                &:nth-child(2){
                    margin-right: 180px;
                }

                &:nth-child(3){
                    margin-right: 100px;
                }

                &:nth-child(4){
                    margin-right: 100px;
                }
            }
        }
    }
}