.message-block{
    padding: 32px 52px 33px 72px;
    box-sizing: border-box;
    position: relative;
    &-header{
        display: flex;
        justify-content: space-between;
        margin-bottom: 200px;

        &-left{
            &-top{
                display: flex;
                margin-bottom: 24px;

                &__img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    margin-right: 16px;
                }
            }

            .last-seen{
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 16px;
                color: #686871;
            }
        }
    }

    &-center{
        display: flex;
        padding-bottom: 40px;
        border-bottom: 1px solid #E1E6ED;
        margin-bottom: 35px;

        &__img{
            filter: drop-shadow(0px 4px 20px rgba(4, 11, 35, 0.16));
            border-radius: 20px;
            width: 150px;
            height: 118px;
            object-fit: cover;
            margin-right: 24px;
        }
    }

    .owner-chat{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 140px;

        &__name{
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.233333px;
            color: #25252B;
            margin-right: 17px;
        }

        &__img{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }
    }

    &-footer{
        &__text-holder{
            background: #FFFFFF;
            border: 1px solid #EBECF0;
            border-radius: 5px;
            resize: none;
            width: 100%;
            padding: 16px 24px;
            box-sizing: border-box;
            margin-bottom: 16px;
        }

        .send-block{
            display: flex;
            justify-content: space-between;

            .script-block{
                width: 20px;
                height: 20px;
                input{
                    display: none;
                }

                .script-block__label{
                    cursor: pointer;
                    img{
                        width: 15px;
                        height: 20px;
                    }
                }
            }

            &__btn{
                background: #41D481;
                box-shadow: 0px 2px 24px rgba(72, 128, 255, 0.2);
                border-radius: 20px;
                padding: 11px 48px 11px 21px;
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                color: #FFFFFF;
                border: none;
                position: relative;
                cursor: pointer;

                &::after{
                    content: '';
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    right: 10px;
                    margin: auto;
                    background-repeat: no-repeat;
                    width: 26px;
                    height: 13px;
                    background-image: url(../../assets/images/right-arrow.svg);
                    background-size: contain;
                }
            }
        }
    }
}