.card{
    padding: 30px 26px 36px 26px;
    background: #FFFFFF;
    border: 1px solid rgba(120, 121, 147, 0.243461);
    box-sizing: border-box;
    width: 357px;
    display: block;

    &__img{
        width: 301.55px;
        height: 218,97px;
        margin-bottom: 13px;
        object-fit: cover;
    }

    &__title{
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        margin-bottom: 12px;
    }

    &__publish{
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        color: #4A4A4A;
        margin-bottom: 5px;
    }

    &__price{
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
    }
}