.proba{
    width: 85px;
    height: 85px;
    margin-bottom: 13px;
    overflow: hidden;
    object-fit: cover;

    .ant-image-preview-img{
        width: 400px !important;
        height: 400px !important;
    }

    &.big{
        width: 456px !important;
        height: 456px !important;
        margin-bottom: 0;
    }

    .ant-image-mask{
        width: 456px !important;
    }
}