.slider-card{
    width: 100px;
    margin-right: 40px;
    position: relative;
    z-index: 42;
    &-top{
        background: #F7E233;
        border-radius: 20px;
        padding: 13px;
        margin-bottom: 6px;
        display: flex;
        justify-content: center;
        align-items: center;

        &.secondary{
            background: #FF9E83;
        }
        &.primary{
            background: #B6CAFB;
        }
    }

    &__title{
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: #4A4A4A;
        text-align: center;
    }
}