.poster-card{
    background: #FFFFFF;
    margin-bottom: 10px;

    &__title{
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #414D6A; 
    }

    &-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 23px 16px 10px 17px;
        box-sizing: border-box;
        border-bottom: 1px solid rgba(151, 151, 151, 0.246886);
        &__img{
            border: 1px solid rgba(151, 151, 151, 0.292346);
            width: 49px;
            height: 49px;
            object-fit: cover;
            border-radius: 50%;
        }

        &__more{
            cursor: pointer;
        }

        &__btn{
            background: #FFFFFF;
            border: 1px solid #41D481;
            box-sizing: border-box;
            border-radius: 100px 5px 5px 100px;
            padding: 8px 20px 10px 47px;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            position: relative;
            cursor: pointer;

            &.btn--added{
                border: 1px solid #304FFF;
                &::before{
                    background-color: #304FFF;
                }
            }

            &::before{
                width: 38px;
                height: 38px;
                content: '';
                position: absolute;
                left: 0;
                background-color: #41D481;
                background-repeat: no-repeat;
                background-image: url(../../assets/images/right-arrow.svg);
                transform: rotate(-90deg);
                border-radius: 50%;
                top: 0;
                bottom: 0;
                margin: auto;
                background-size: 26px 16px;
                background-position: center;

            }
        }

        .poster-card-price{
            &__title{
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #414D6A;
            }
        }

        .poster-card-date{
            width: 10%;
        }

    }

    &-bottom{
        display: flex;
        align-items: center;
        padding: 8px 22px;
        &-statis{
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            color: #304FFF;
            margin-right: 20px;
        }

        .poster-card-date{
            margin-right: 20px;
        }
    }

    .poster-card__date{
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #414D6A;
    }
}