.footer{
    background: #33383C;

    &-top{
        background: #282B30;
        padding: 23px 0;

        &-inner{
            display: flex;
            justify-content: space-between;
            align-items: center;

            &-left{
                display: flex;
                align-items: center;

                a{
                    display: block;
                    margin-right: 14px;
                }

                &__img{
                    width: 67px;
                    height: 67px;
                    opacity: 0.9;
                }

                &__text{
                    font-style: normal;
                    font-weight: bold;
                    width: 159px;
                    font-size: 14px;
                    line-height: 17px;
                    color: #fff !important;
                    position: relative;
                    z-index: 31;
                }
            }

            &-center{
                &__title{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    width: 307px;
                    color: #98A4B7;
                }
            }

            &-right{
                display: flex;
                &-item{
                    &:first-child{
                        margin-right: 5px;
                    }
                }
            }
        }
    }
}